import { Injectable } from '@angular/core';
import { Camera, CameraOptions } from '@ionic-native/camera/ngx';

@Injectable()
export class LoadImageService {

   // base64Image: Promise<string>;

    constructor(private camera: Camera) { }
    
    async getPhoto() {

        const options: CameraOptions = {
            quality: 100,
         //   targetHeight: 300,  
            destinationType: this.camera.DestinationType.DATA_URL,
            sourceType: this.camera.PictureSourceType.PHOTOLIBRARY, 
            encodingType: this.camera.EncodingType.JPEG,
            mediaType: this.camera.MediaType.PICTURE,
            /* correctOrientation: true,
            saveToPhotoAlbum: false,
            allowEdit: false */
        }

    //    return 'data:image/jpeg;base64,' + await this.camera.getPicture(options);
        return new Promise<string>(async (res, reject) => {
            await this.camera.getPicture(options).then( imageData => 
                res('data:image/jpeg;base64,' + imageData),
            )
            reject("");
        });

       

        // Parte sottostante da eliminare. L'unica cosa da attenzionare nella parte sottostante è che viene gestito un caso d'errore, cosa che sopra non viene considerato
        /*  this.base64Image = await this.camera.getPicture(options).then((imageData) => {
            this.base64Image = 'data:image/jpeg;base64,' + imageData;
        }, (err) => {
        // Handle error
        console.log("Error to capture image from gallery: ", err);
        }); */
    //   return this.base64Image;
    }
}